import { useState } from "react";
import { Copy, LogOut } from "lucide-react";

export default function WalletInterface() {
  const [address] = useState("0x1234...5678");
  const [balance] = useState("1,234.56");
  const [amount, setAmount] = useState("1");
  const [activeTab, setActiveTab] = useState("deposit");

  const handleCopy = () => {
    navigator.clipboard.writeText(address);
    // You could add a toast notification here
  };

  const handleLogout = () => {
    // Handle logout logic
    console.log("Logging out...");
  };

  const handleConfirm = () => {
    if (activeTab === "deposit") {
      console.log(`Depositing ${amount}`);
    } else {
      console.log(`Withdrawing ${amount}`);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-3xl shadow-lg p-8 w-full max-w-md">
        {/* Header with address and buttons */}
        <div className="flex items-center justify-between mb-8">
          <div className="flex-1">
            <span className="text-gray-600 text-sm">address</span>
            <div className="font-mono text-sm mt-1">{address}</div>
          </div>
          <div className="flex gap-2 ml-4">
            <button
              onClick={handleCopy}
              className="p-2 border-2 border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
              title="Copy address"
            >
              <Copy size={16} />
            </button>
            <button
              onClick={handleLogout}
              className="p-2 border-2 border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
              title="Logout"
            >
              <LogOut size={16} />
            </button>
          </div>
        </div>

        {/* Balance */}
        <div className="text-center mb-8">
          <div className="text-gray-600 text-sm mb-2">Balance</div>
          <div className="text-3xl font-semibold">{balance}</div>
        </div>

        {/* Deposit/Withdraw Tabs */}
        <div className="flex gap-2 mb-6">
          <button
            onClick={() => setActiveTab("deposit")}
            className={`flex-1 py-3 px-6 rounded-xl font-medium transition-colors ${
              activeTab === "deposit"
                ? "bg-red-500 text-white border-2 border-red-500"
                : "bg-white text-gray-700 border-2 border-gray-300 hover:bg-gray-50"
            }`}
          >
            Deposit
          </button>
          <button
            onClick={() => setActiveTab("withdraw")}
            className={`flex-1 py-3 px-6 rounded-xl font-medium transition-colors ${
              activeTab === "withdraw"
                ? "bg-gray-800 text-white border-2 border-gray-800"
                : "bg-white text-gray-700 border-2 border-gray-300 hover:bg-gray-50"
            }`}
          >
            Withdraw
          </button>
        </div>

        {/* Amount Input */}
        <div className="mb-6">
          <label className="block text-gray-600 text-sm mb-2">Amount</label>
          <input
            type="number"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
            className="w-full py-4 px-6 text-lg border-2 border-gray-300 rounded-xl focus:outline-none focus:border-blue-500 transition-colors"
            placeholder="Enter amount"
          />
        </div>

        {/* Confirm Button */}
        <button
          onClick={handleConfirm}
          className="w-auto px-8 py-3 bg-gray-800 text-white rounded-xl font-medium hover:bg-gray-700 transition-colors float-right"
        >
          Confirm
        </button>
        <div className="clear-both"></div>
      </div>
    </div>
  );
}
